<template>
    <div class="maxBox">
        <el-input v-model="input" placeholder="输入服务器地址或实例ID进行查询" class="search-input" ></el-input>
         <el-button type="primary">查询</el-button>
         <div>
           <div class="box null-box">
                <div class="toolBar">
                    <el-tabs v-model="activeName" @tab-click="handleClick" style="margin-left:30px">
                    <el-tab-pane label="CPU指标" name="1"><div class="content" id="cpuTarget"></div></el-tab-pane>
                    <el-tab-pane label="内存指标" name="2"><div class="content" id="memoryTarget"></div></el-tab-pane>
                    <el-tab-pane label="网络指标" name="3">
                        <div class="content" id="netWorkIn"></div>
                        <div class="content" id="netWorkOut"></div>
                        </el-tab-pane>
                    <el-tab-pane label="硬盘指标" name="4">
                         <div class="content" id="hardDisk"></div>
                         <div class="content" id="ioRead"></div>
                         <div class="content" id="ioWrite"></div>
                    </el-tab-pane>
                    </el-tabs>
                </div>
              
          </div>
        <DateSearch @func="changeDate"></DateSearch>
         </div>
    </div>
</template>
<script>
import { EleResize} from "../../utils/resize";
import DateSearch from './components/DateSearch'
export default {
      data() {
        return {
        input: '',
        activeName: '1'
        };
    },
    mounted(){
        this.$nextTick(() => {
            this.cpuTargetFun();
        });
    },
    methods: {
       /**
       * 日期返回值
       */
      changeDate(msg){
        console.log(msg);
      }, 
      handleClick(tab, event) {
        if(tab.name==1){
        this.$nextTick(() => {
            this.cpuTargetFun(); 
        });
        }else if(tab.name==2){
        this.$nextTick(() => {
             this.memoryTargetFun();
        });
       }else if(tab.name==3){
        this.$nextTick(() => {
            this.netWorkInFun();
            this.netWorkOutFun();
        });
       }else if(tab.name==4){
        this.$nextTick(() => {
           this.hardDiskFun();
           this.ioReadFun();
           this.ioWriteFun();
        });
       }
      },
    /**
     * CPU指标
     */
    cpuTargetFun(){
        let myChart = this.$echarts.init(document.getElementById('cpuTarget'));
        let option = {
                grid:{
                     top:'15%',
                     left:'2%',
                     right:'2%',
                     bottom:'10%',
                     containLabel:true
                    },
                tooltip: {
                    trigger: 'axis'
                },
                xAxis: {
                    type: 'category',
                    data: ['2021-05-22', '2021-05-23', '2021-05-24', '2021-05-25', '2021-05-26', '2021-05-27', '2021-05-28'],
                },
                yAxis: {
                    type: 'value',
                    name:'cpu使用率(%)',
                    nameTextStyle:{
                      fontWeight:'bold',
                      fontSize:16
                    },
                    axisLine:{    
                        show:false
                    },
                    axisTick:{    
                        show:false
                    },
                    splitLine: { 
                        lineStyle:{
                            type:'dashed'
                    }
                        }                                     
                },
                 legend: {
                    data: ['CPU',]
                   },
                series: [{
                    name: "CPU",
                    data: [150, 230, 224, 218, 135, 147, 260],
                    type: 'line',
                    itemStyle : {  
                            normal : {  
                                color:'#409eff',  
                                lineStyle:{  
                                    color:'#409eff'  
                                }  
                            }  
                        },  
                }]
            };
            myChart.setOption(option);
           let resizeDiv = document.getElementById('cpuTarget')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
    },
     /**
     * 内存指标
     */
    memoryTargetFun(){
        let myChart = this.$echarts.init(document.getElementById('memoryTarget'));
        let option = {
                grid:{
                     top:'15%',
                     left:'2%',
                     right:'2%',
                     bottom:'10%',
                     containLabel:true
                    },
                tooltip: {
                    trigger: 'axis'
                },
                xAxis: {
                    type: 'category',
                    data: ['2021-05-22', '2021-05-23', '2021-05-24', '2021-05-25', '2021-05-26', '2021-05-27', '2021-05-28'],
                },
                yAxis: {
                    type: 'value',
                    name:'内存使用率(%)',
                    nameTextStyle:{
                      fontWeight:'bold',
                      fontSize:16
                    },
                    axisLine:{    
                        show:false
                    },
                    axisTick:{    
                        show:false
                    },
                    splitLine: { 
                        lineStyle:{
                            type:'dashed'
                    }
                        }                                     
                },
                 legend: {
                    data: ['内存',]
                   },
                series: [{
                    name: "内存",
                    data: [150, 230, 224, 218, 135, 147, 260],
                    type: 'line',
                    itemStyle : {  
                            normal : {  
                                color:'#409eff',  
                                lineStyle:{  
                                    color:'#409eff'  
                                }  
                            }  
                        },  
                }]
            };
            myChart.setOption(option,true);
           let resizeDiv = document.getElementById('memoryTarget')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
    },
    /**
     * 网络流入
     */
    netWorkInFun(){
        let myChart = this.$echarts.init(document.getElementById('netWorkIn'));
        let option = {
                grid:{
                     top:'15%',
                     left:'3%',
                     right:'2%',
                     bottom:'10%',
                     containLabel:true
                    },
                tooltip: {
                    trigger: 'axis'
                },
                xAxis: {
                    type: 'category',
                    data: ['2021-05-22', '2021-05-23', '2021-05-24', '2021-05-25', '2021-05-26', '2021-05-27', '2021-05-28'],
                },
                yAxis: {
                    type: 'value',
                    name:'网络流入速率(Mbps)',
                    nameTextStyle:{
                      fontWeight:'bold',
                      fontSize:16
                    },
                    axisLine:{    
                        show:false
                    },
                    axisTick:{    
                        show:false
                    },
                    splitLine: { 
                        lineStyle:{
                            type:'dashed'
                    }
                        }                                     
                },
                 legend: {
                    data: ['网络流入速率',]
                   },
                series: [{
                    name: "网络流入速率",
                    data: [150, 230, 224, 218, 135, 147, 260],
                    type: 'line',
                    itemStyle : {  
                            normal : {  
                                color:'#409eff',  
                                lineStyle:{  
                                    color:'#409eff'  
                                }  
                            }  
                        },  
                }]
            };
            myChart.setOption(option,true);
          let resizeDiv = document.getElementById('netWorkIn')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
    },
    /**
     * 网络流出
     */
    netWorkOutFun(){
        let myChart = this.$echarts.init(document.getElementById('netWorkOut'));
        let option = {
                grid:{
                     top:'15%',
                     left:'3%',
                     right:'2%',
                     bottom:'10%',
                     containLabel:true
                    },
                tooltip: {
                    trigger: 'axis'
                },
                xAxis: {
                    type: 'category',
                    data: ['2021-05-22', '2021-05-23', '2021-05-24', '2021-05-25', '2021-05-26', '2021-05-27', '2021-05-28'],
                },
                yAxis: {
                    type: 'value',
                    name:'网络流出速率(Mbps)',
                    nameTextStyle:{
                      fontWeight:'bold',
                      fontSize:16
                    },
                    axisLine:{    
                        show:false
                    },
                    axisTick:{    
                        show:false
                    },
                    splitLine: { 
                        lineStyle:{
                            type:'dashed'
                    }
                        }                                     
                },
                 legend: {
                    data: ['网络流出速率',]
                   },
                series: [{
                    name: "网络流出速率",
                    data: [150, 230, 224, 218, 135, 147, 260],
                    type: 'line',
                    itemStyle : {  
                            normal : {  
                                color:'green',  
                                lineStyle:{  
                                    color:'green'  
                                }  
                            }  
                        },  
                }]
            };
            myChart.setOption(option,true);
           let resizeDiv = document.getElementById('netWorkOut')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
    },
    /**
     * 硬盘使用率
     */
    hardDiskFun(){
        let myChart = this.$echarts.init(document.getElementById('hardDisk'));
        let option = {
                grid:{
                    top:'15%',
                     left:'2%',
                     right:'2%',
                     bottom:'10%',
                     containLabel:true
                    },
                tooltip: {
                    trigger: 'axis'
                },
                xAxis: {
                    type: 'category',
                    data: ['2021-05-22', '2021-05-23', '2021-05-24', '2021-05-25', '2021-05-26', '2021-05-27', '2021-05-28'],
                },
                yAxis: {
                    type: 'value',
                    name:'硬盘使用率(%)',
                    nameTextStyle:{
                      fontWeight:'bold',
                      fontSize:16
                    },
                    axisLine:{    
                        show:false
                    },
                    axisTick:{    
                        show:false
                    },
                    splitLine: { 
                        lineStyle:{
                            type:'dashed'
                    }
                        }                                     
                },
                 legend: {
                    data: ['硬盘使用率',]
                   },
                series: [{
                    name: "硬盘使用率",
                    data: [150, 230, 224, 218, 135, 147, 260],
                    type: 'line',
                    itemStyle : {  
                            normal : {  
                                color:'#409eff',  
                                lineStyle:{  
                                    color:'#409eff'  
                                }  
                            }  
                        },  
                }]
            };
            myChart.setOption(option,true);
           let resizeDiv = document.getElementById('hardDisk')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
    },
    /**
     * IO读速率
     */
    ioReadFun(){
        let myChart = this.$echarts.init(document.getElementById('ioRead'));
        let option = {
                grid:{
                  top:'15%',
                     left:'2%',
                     right:'2%',
                     bottom:'10%',
                     containLabel:true
                    },
                tooltip: {
                    trigger: 'axis'
                },
                xAxis: {
                    type: 'category',
                    data: ['2021-05-22', '2021-05-23', '2021-05-24', '2021-05-25', '2021-05-26', '2021-05-27', '2021-05-28'],
                },
                yAxis: {
                    type: 'value',
                    name:'IO读速率(KB/s)',
                    nameTextStyle:{
                      fontWeight:'bold',
                      fontSize:16
                    },
                    axisLine:{    
                        show:false
                    },
                    axisTick:{    
                        show:false
                    },
                    splitLine: { 
                        lineStyle:{
                            type:'dashed'
                    }
                        }                                     
                },
                 legend: {
                    data: ['IO读速率',]
                   },
                series: [{
                    name: "IO读速率",
                    data: [150, 230, 224, 218, 135, 147, 260],
                    type: 'line',
                    itemStyle : {  
                            normal : {  
                                color:'green',  
                                lineStyle:{  
                                    color:'green'  
                                }  
                            }  
                        },  
                }]
            };
            myChart.setOption(option,true);
           let resizeDiv = document.getElementById('ioRead')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
    },
    /**
     * IO写速率
     */
    ioWriteFun(){
        let myChart = this.$echarts.init(document.getElementById('ioWrite'));
        let option = {
                grid:{
                    top:'15%',
                     left:'2%',
                     right:'2%',
                     bottom:'10%',
                     containLabel:true
                    },
                tooltip: {
                    trigger: 'axis'
                },
                xAxis: {
                    type: 'category',
                    data: ['2021-05-22', '2021-05-23', '2021-05-24', '2021-05-25', '2021-05-26', '2021-05-27', '2021-05-28'],
                },
                yAxis: {
                    type: 'value',
                    name:'IO写速率(KB/s)',
                    nameTextStyle:{
                      fontWeight:'bold',
                      fontSize:16
                    },
                    axisLine:{    
                        show:false
                    },
                    axisTick:{    
                        show:false
                    },
                    splitLine: { 
                        lineStyle:{
                            type:'dashed'
                    }
                        }                                     
                },
                 legend: {
                    data: ['IO写速率',]
                   },
                series: [{
                    name: "IO写速率",
                    data: [150, 230, 224, 218, 135, 147, 260],
                    type: 'line',
                    itemStyle : {  
                            normal : {  
                                color:'orange',  
                                lineStyle:{  
                                    color:'orange'  
                                }  
                            }  
                        },  
                }]
            };
            myChart.setOption(option,true);
          let resizeDiv = document.getElementById('ioWrite')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
    },
    
    },
    components:{
          DateSearch
        }
  
}
</script>
<style lang="less" scoped>
 @import "index.less";
.search-input{
    width: 250px;
    margin: 15px 0px 15px 0px;
}

.right{
    height: 100%;
}
.block{
    height: 50px;
}
/deep/ .el-tabs__content{
    top: 65px;
    width:100%
}
.content{
    height: 300px;
    width: 100%;
    border: 1px solid #ccc;
    margin-bottom: 15px;
}
</style>


